.nav-icon {
  width: 24px;
  height: 24px;
  position: relative;
  cursor: pointer;

  &__line {
    background-color: $c-white;
    position: absolute;
    border-radius: 2px;
    transition: $transition-speed cubic-bezier(0.8, 0.5, 0.2, 1.4);
    width: 75%;
    height: 10%;
    transition-duration: $transition-speed;
    overflow: visible;

    &--order--1 {
      top: 30%;
      left: 3px;
    }

    &--order--2 {
      bottom: 30%;
      left: 3px;
    }
  }

  &.open &__line--order--1 {
    transform: rotate(45deg);
    top: 13px;
    background-color: $c-white;
  }

  &.open &__line--order--2 {
    transform: rotate(-45deg);
    top: 13px;
    background-color: $c-white;
  }

  &:not(.open):hover &__line--order--1 {
    transform: rotate(-3deg) scaleY(1.1);
  }

  &:not(.open):hover &__line--order--2 {
    transform: rotate(-4deg) scaleY(1.1);
  }
}
